import React from 'react';
import PropTypes from 'prop-types';
// import { iconList } from './../../../../utilities/icons'
import icomoonSelection from './../../../../font/icons/selection.json'
import './icon.css';

const iconList = icomoonSelection.icons.map((icon) => icon.properties.name);


console.log("iconLisssst", iconList)



export const Icon = ({ iconName, color }) => {
  return (
    <i 
      className={`icon-${iconName}`} 
      style={{ color }} 
      aria-hidden="true"
    ></i>
  );
};

Icon.propTypes = {
  iconName: PropTypes.oneOf(iconName).isRequired, // Nome dell'icona (senza "icon-")
  color: PropTypes.string, // Colore dell'icona
};

Icon.defaultProps = {
  color: '#000', // Default: Nero
};
